import React, { useState, useEffect } from 'react';
import { Search, MapPin, Globe, Star, Clock, Filter, ArrowUpDown, Moon, Sun } from 'lucide-react';
import './../styles/guides.css';

// Sample data for guides
const initialGuides = [
  {
    id: 1,
    name: "Sarah Johnson",
    photo: "https://randomuser.me/api/portraits/women/44.jpg",
    location: "Paris, France",
    languages: ["English", "French", "Spanish"],
    experience: 7,
    rating: 4.8,
    specialties: ["Art History", "Food Tours", "Architecture"]
  },
  {
    id: 2,
    name: "Marco Romano",
    photo: "https://randomuser.me/api/portraits/men/32.jpg",
    location: "Rome, Italy",
    languages: ["English", "Italian", "German"],
    experience: 5,
    rating: 4.6,
    specialties: ["Ancient History", "Vatican Tours", "Photography"]
  },
  {
    id: 3,
    name: "Yuki Tanaka",
    photo: "https://randomuser.me/api/portraits/women/22.jpg",
    location: "Tokyo, Japan",
    languages: ["English", "Japanese"],
    experience: 9,
    rating: 4.9,
    specialties: ["Cultural Experiences", "Temple Tours", "Sushi Making"]
  },
  {
    id: 4,
    name: "Carlos Mendez",
    photo: "https://randomuser.me/api/portraits/men/65.jpg",
    location: "Barcelona, Spain",
    languages: ["English", "Spanish", "Catalan"],
    experience: 4,
    rating: 4.5,
    specialties: ["Gaudi Architecture", "Tapas Tours", "Flamenco"]
  },
  {
    id: 5,
    name: "Emma Wilson",
    photo: "https://randomuser.me/api/portraits/women/68.jpg",
    location: "London, UK",
    languages: ["English", "French"],
    experience: 6,
    rating: 4.7,
    specialties: ["Royal History", "Pub Crawls", "Harry Potter Tours"]
  },
  {
    id: 6,
    name: "Raj Patel",
    photo: "https://randomuser.me/api/portraits/men/55.jpg",
    location: "Mumbai, India",
    languages: ["English", "Hindi", "Gujarati"],
    experience: 8,
    rating: 4.9,
    specialties: ["Bollywood Tours", "Street Food", "Spiritual Walks"]
  }
];

const GuideDashboard = () => {
    const [filteredGuides, setFilteredGuides] = useState(initialGuides);
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedLocation, setSelectedLocation] = useState('');
    const [selectedLanguage, setSelectedLanguage] = useState('');
    const [sortBy, setSortBy] = useState('rating');
    const [sortOrder, setSortOrder] = useState('desc');
    const [viewMode, setViewMode] = useState('card');
    const [theme, setTheme] = useState(
      localStorage.getItem('theme') || 
      (window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light')
    );
  
    // Extract unique locations and languages for filters
    const allLocations = [...new Set(initialGuides.map(guide => guide.location))];
    const allLanguages = [...new Set(initialGuides.flatMap(guide => guide.languages))];

  // Handle search and filtering
  useEffect(() => {
    let result = [...initialGuides];
    
    if (searchQuery) {
      result = result.filter(guide => 
        guide.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        guide.specialties.some(s => s.toLowerCase().includes(searchQuery.toLowerCase()))
      );
    }
    
    if (selectedLocation) {
      result = result.filter(guide => guide.location === selectedLocation);
    }
    
    if (selectedLanguage) {
      result = result.filter(guide => guide.languages.includes(selectedLanguage));
    }
    
    result.sort((a, b) => {
      const aValue = a[sortBy];
      const bValue = b[sortBy];
      
      if (sortOrder === 'asc') {
        return aValue > bValue ? 1 : -1;
      } else {
        return aValue < bValue ? 1 : -1;
      }
    });
    
    setFilteredGuides(result);
  }, [searchQuery, selectedLocation, selectedLanguage, sortBy, sortOrder]);

  // Toggle sort order
  const toggleSortOrder = () => {
    setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
  };

  // Handle book now button click
  const handleBookNow = (guideId) => {
    console.log(`Booking guide with ID: ${guideId}`);
    // Add your booking logic here
  };

  // Toggle theme
  const toggleTheme = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
    localStorage.setItem('theme', newTheme);
    document.documentElement.setAttribute('data-theme', newTheme);
  };

  // Render star rating
  const renderStars = (rating) => {
    return (
      <div className="star-rating flex items-center">
        {[...Array(5)].map((_, i) => {
          const starValue = i + 1;
          return (
            <div key={i} className="relative">
              <Star 
                className={`w-4 h-4 ${starValue <= Math.floor(rating) ? 'fill-yellow-400 text-yellow-400' : 'text-gray-300 dark:text-gray-600'}`}
              />
              {rating % 1 > 0 && Math.floor(rating) === i && (
                <div className="absolute top-0 left-0 overflow-hidden" style={{ width: `${(rating % 1) * 100}%` }}>
                  <Star className="w-4 h-4 fill-yellow-400 text-yellow-400" />
                </div>
              )}
            </div>
          );
        })}
        <span className="ml-1 text-sm text-gray-600 dark:text-gray-400">{rating.toFixed(1)}</span>
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-50 dark:from-gray-900 dark:to-gray-800 transition-colors duration-300">
      {/* Header with glass effect */}
      <header className="sticky-filters glass-card p-4 md:p-6 shadow-sm">
        <div className="container mx-auto flex justify-between items-center">
          <div>
            <h1 className="text-2xl md:text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-purple-600 dark:from-blue-400 dark:to-purple-400">
              Find Your Perfect Guide
            </h1>
            <p className="mt-2 text-gray-600 dark:text-gray-300">
              Discover experienced local guides for your next adventure
            </p>
          </div>
          
          {/* Dark mode toggle */}
          <button 
            onClick={toggleTheme}
            className="theme-toggle flex items-center justify-between px-2"
            aria-label="Toggle dark mode"
          >
            {theme === 'dark' ? (
              <Sun className="w-4 h-4 text-yellow-300" />
            ) : (
              <Moon className="w-4 h-4 text-gray-700" />
            )}
          </button>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto p-4 md:p-6">
        {/* Search and Filters - Glass panel */}
        <div className="glass-card rounded-xl shadow-sm p-4 mb-6">
          <div className="flex flex-col md:flex-row md:items-center gap-4">
            {/* Search */}
            <div className="relative flex-grow">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Search className="h-5 w-5 text-gray-400 dark:text-gray-500" />
              </div>
              <input
                type="text"
                placeholder="Search guides by name or specialty..."
                className="pl-10 pr-4 py-2 w-full bg-white/70 dark:bg-gray-800/70 border border-gray-200 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:focus:ring-blue-400 dark:focus:border-blue-400 transition-all"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            
            {/* Location Filter */}
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <MapPin className="h-5 w-5 text-gray-400 dark:text-gray-500" />
              </div>
              <select
                className="pl-10 pr-4 py-2 bg-white/70 dark:bg-gray-800/70 border border-gray-200 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:focus:ring-blue-400 dark:focus:border-blue-400 transition-all"
                value={selectedLocation}
                onChange={(e) => setSelectedLocation(e.target.value)}
              >
                <option value="">All Locations</option>
                {allLocations.map(location => (
                  <option key={location} value={location}>{location}</option>
                ))}
              </select>
            </div>
            
            {/* Language Filter */}
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Globe className="h-5 w-5 text-gray-400 dark:text-gray-500" />
              </div>
              <select
                className="pl-10 pr-4 py-2 bg-white/70 dark:bg-gray-800/70 border border-gray-200 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:focus:ring-blue-400 dark:focus:border-blue-400 transition-all"
                value={selectedLanguage}
                onChange={(e) => setSelectedLanguage(e.target.value)}
              >
                <option value="">All Languages</option>
                {allLanguages.map(language => (
                  <option key={language} value={language}>{language}</option>
                ))}
              </select>
            </div>
          </div>

          <div className="flex flex-col md:flex-row md:items-center justify-between mt-4 gap-4">
            {/* Sort Options */}
            <div className="flex items-center space-x-2">
              <label className="text-sm text-gray-600 dark:text-gray-400 whitespace-nowrap">Sort by:</label>
              <select
                className="px-3 py-2 bg-white/70 dark:bg-gray-800/70 border border-gray-200 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:focus:ring-blue-400 dark:focus:border-blue-400 transition-all"
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
              >
                <option value="rating">Rating</option>
                <option value="experience">Experience</option>
                <option value="name">Name</option>
              </select>
              <button 
                onClick={toggleSortOrder}
                className="p-2 bg-white/70 dark:bg-gray-800/70 border border-gray-200 dark:border-gray-700 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 transition-all"
                aria-label={sortOrder === 'asc' ? 'Sort ascending' : 'Sort descending'}
              >
                <ArrowUpDown className="h-5 w-5 text-gray-500 dark:text-gray-400" />
              </button>
            </div>
            
            {/* View Toggle */}
            <div className="flex items-center space-x-2">
              <button 
                onClick={() => setViewMode('card')} 
                className={`px-3 py-2 rounded-lg transition-all ${viewMode === 'card' ? 'bg-blue-100 dark:bg-blue-900/50 text-blue-600 dark:text-blue-400' : 'bg-white/70 dark:bg-gray-800/70 text-gray-600 dark:text-gray-400'}`}
              >
                Cards
              </button>
              <button 
                onClick={() => setViewMode('table')} 
                className={`px-3 py-2 rounded-lg transition-all ${viewMode === 'table' ? 'bg-blue-100 dark:bg-blue-900/50 text-blue-600 dark:text-blue-400' : 'bg-white/70 dark:bg-gray-800/70 text-gray-600 dark:text-gray-400'}`}
              >
                Table
              </button>
            </div>
          </div>
          
          {/* Results Count */}
          <div className="mt-4 text-sm text-gray-600 dark:text-gray-400">
            Showing {filteredGuides.length} {filteredGuides.length === 1 ? 'guide' : 'guides'}
          </div>
        </div>
        
        {/* Guides List - Card View */}
        {viewMode === 'card' && (
          <div className="guide-grid grid gap-6 view-transition">
            {filteredGuides.map(guide => (
              <div 
                key={guide.id} 
                className="guide-card neumorphic-card overflow-hidden hover:shadow-lg transition-all duration-300"
              >
                <div className="relative h-48 bg-gradient-to-r from-blue-100 to-indigo-100 dark:from-gray-700 dark:to-gray-800">
                  <img 
                    src={guide.photo} 
                    alt={guide.name}
                    className="w-full h-full object-cover"
                    onError={(e) => {
                      e.target.onerror = null;
                      e.target.src = `https://ui-avatars.com/api/?name=${encodeURIComponent(guide.name)}&background=3a86ff&color=fff&size=200`;
                    }}
                  />
                  <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/60 to-transparent p-4">
                    <h3 className="text-xl font-semibold text-white">{guide.name}</h3>
                  </div>
                </div>
                
                <div className="p-5">
                  <div className="flex flex-col gap-3">
                    <div className="flex items-center text-gray-700 dark:text-gray-300">
                      <MapPin className="w-5 h-5 mr-2 text-blue-500 dark:text-blue-400" />
                      <span>{guide.location}</span>
                    </div>
                    
                    <div className="flex items-center text-gray-700 dark:text-gray-300">
                      <Globe className="w-5 h-5 mr-2 text-blue-500 dark:text-blue-400" />
                      <span>{guide.languages.join(', ')}</span>
                    </div>
                    
                    <div className="flex items-center text-gray-700 dark:text-gray-300">
                      <Clock className="w-5 h-5 mr-2 text-blue-500 dark:text-blue-400" />
                      <span>{guide.experience} {guide.experience === 1 ? 'year' : 'years'} experience</span>
                    </div>
                    
                    <div>
                      <p className="text-sm text-gray-600 dark:text-gray-400">Specialties:</p>
                      <div className="flex flex-wrap gap-2 mt-1">
                        {guide.specialties.map((specialty, index) => (
                          <span 
                            key={index} 
                            className="text-xs px-2 py-1 bg-blue-100 dark:bg-blue-900/50 text-blue-800 dark:text-blue-200 rounded-full"
                          >
                            {specialty}
                          </span>
                        ))}
                      </div>
                    </div>
                    
                    <div className="mt-2">
                      {renderStars(guide.rating)}
                    </div>
                    
                    <button 
                      onClick={() => handleBookNow(guide.id)}
                      className="mt-4 w-full bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white font-medium py-2 px-4 rounded-lg transition-all transform hover:scale-[1.02] shadow-md hover:shadow-lg"
                    >
                      Book Now
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
        
        {/* Guides List - Table View */}
        {viewMode === 'table' && (
          <div className="view-transition overflow-hidden rounded-xl shadow-sm">
            <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700 bg-white/70 dark:bg-gray-800/70">
              <thead className="bg-gray-50/80 dark:bg-gray-700/80">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                    Guide
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                    Location
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                    Languages
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                    Experience
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                    Rating
                  </th>
                  <th scope="col" className="px-6 py-3 text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200 dark:divide-gray-700">
                {filteredGuides.map(guide => (
                  <tr key={guide.id} className="hover:bg-gray-50/50 dark:hover:bg-gray-700/50 transition-colors">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10">
                          <img 
                            className="h-10 w-10 rounded-full object-cover"
                            src={guide.photo}
                            alt={guide.name}
                            onError={(e) => {
                              e.target.onerror = null;
                              e.target.src = `https://ui-avatars.com/api/?name=${encodeURIComponent(guide.name)}&background=3a86ff&color=fff&size=200`;
                            }}
                          />
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900 dark:text-gray-100">
                            {guide.name}
                          </div>
                          <div className="text-xs text-gray-500 dark:text-gray-400">
                            {guide.specialties.slice(0, 2).join(', ')}
                            {guide.specialties.length > 2 && '...'}
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 dark:text-gray-400">
                      {guide.location}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 dark:text-gray-400">
                      {guide.languages.join(', ')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 dark:text-gray-400">
                      {guide.experience} {guide.experience === 1 ? 'year' : 'years'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      {renderStars(guide.rating)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-center">
                      <button 
                        onClick={() => handleBookNow(guide.id)}
                        className="bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white text-sm font-medium py-1 px-3 rounded-lg transition-all shadow-md hover:shadow-lg"
                      >
                        Book Now
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
        
        {/* No Results Message */}
        {filteredGuides.length === 0 && (
          <div className="text-center py-16 glass-card rounded-xl">
            <Filter className="mx-auto h-12 w-12 text-gray-400 dark:text-gray-500" />
            <h3 className="mt-4 text-xl font-medium text-gray-900 dark:text-gray-100">
              No guides found
            </h3>
            <p className="mt-2 text-gray-600 dark:text-gray-400">
              Try adjusting your search or filter criteria
            </p>
            <button
              onClick={() => {
                setSearchQuery('');
                setSelectedLocation('');
                setSelectedLanguage('');
              }}
              className="mt-6 inline-flex items-center px-4 py-2 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-all"
            >
              Clear all filters
            </button>
          </div>
        )}
      </main>
      
      {/* Footer */}
      <footer className="bg-gray-800/90 text-white p-4 md:p-6 mt-8 backdrop-blur-sm">
        <div className="container mx-auto text-center">
          <p>&copy; {new Date().getFullYear()} Travel Guide Booking. All rights reserved.</p>
        </div>
      </footer>
    </div>
  );
};

export default GuideDashboard;